import React from 'react';
import './VideoTree.css';
import PropTypes from 'prop-types';

import { buildStructure, flattenStructure } from '../../utils/tree.js';

import NodeExpansion from './NodeExpansion.js';

class VideoTree extends React.Component {
    
    constructor(props) {
        super(props);

        this.state = {
            expandNode: 0, // node.id
            nodes: [],
        };

        this.toggleExpandNode = this.toggleExpandNode.bind(this);
        this.updateNode = this.updateNode.bind(this);
        this.availableParents = this.availableParents.bind(this);
        this.createNode = this.createNode.bind(this);
        this.deleteNode = this.deleteNode.bind(this);

    }

    toggleExpandNode(e, nodeId) {
        this.setState({expandNode: nodeId});
    }

    availableParents(node, tree) {
        const flattenedNode = flattenStructure(node);
        const nodeIds = flattenedNode.map(n => n.id);
        return tree.filter(n => {
            return nodeIds.indexOf(n.id) === -1  && n.id !== node.parent_id
        });
    }

    createNode(e, nodeId) {
        e.stopPropagation();
        // e.nativeEvent.stopImmediatePropagation();
        this.props.createNode(nodeId);
        
    }

    deleteNode(e, nodeId) {
        e.stopPropagation();
        // e.nativeEvent.stopImmediatePropagation();
        this.props.deleteNode(nodeId);
    }

    updateNode(node) {
        setTimeout(_ => this.setState({expandNode: 0}), 100);
        this.props.updateNode(node);
    }

    buildTree(nodes, nodesKey) {
        return (
            <ul>
            { 
                nodes.map(node => {
                    let nodeInactiveClass = '';
                    if (node.parent_id > 0) {
                        const parent = this.props.nodes.filter(n => {
                            return n.id === node.parent_id;
                        });

                        nodeInactiveClass = typeof parent[0] !== 'undefined' && parent[0].interactor.enabled !== true ? 'node-inactive' : '';
                    }
                    return (
                        <li key={'li_'+node.id}>
                            <span style={{position: 'relative'}}
                                onClick={(e) => this.toggleExpandNode(e, node.id)} 
                                className={`node-title ${nodeInactiveClass}`}
                            >
                                {node.title}
                                <button onClick={(e) => this.createNode(e, node.id)}>+</button>
                                <button onClick={(e) => this.deleteNode(e, node.id)}>-</button>

                                {
                                    this.state.expandNode === node.id 
                                        ?   <NodeExpansion 
                                                node={node} 
                                                availableParents={this.availableParents(node, this.props.nodes)}
                                                updateNode={this.updateNode} 
                                            />
                                        : ''
                                }
                                
                            </span>
                            { 
                                typeof node[nodesKey] !== 'undefined' && node[nodesKey].length > 0
                                    ? this.buildTree(node[nodesKey], nodesKey) 
                                    : '' 
                            }
                        </li>
                    );    
                })
            }
            </ul>
        );
    }

    componentDidMount() {
        const nodes = [buildStructure(this.props.nodes)];
        this.setState({nodes});
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (prevProps.nodes !== this.props.nodes) {
            const nodes = [buildStructure(this.props.nodes)];
            this.setState({nodes});
        }
    }

    render() {
        // e.g. your child nodes might have a different name so you can pass the key as a prop.
        const nodesKey = this.props.nodesKey ? this.props.nodesKey : 'nodes';
        return (
            <div>
                <div className="tree">
                    {this.buildTree(this.state.nodes, nodesKey)}
                </div>
            </div>
        );
    }
}

VideoTree.propTypes = {
    nodesKey: PropTypes.string,
    nodes: PropTypes.array.isRequired,
    createNode: PropTypes.func.isRequired,
    updateNode: PropTypes.func.isRequired,
};

export default VideoTree;

