import React from 'react';
import { Link } from 'react-router-dom';
import PropTypes from 'prop-types';

class Item extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            editing: false,
            name: '',
        };

        this.toggleEditing = this.toggleEditing.bind(this);
        this.updateName = this.updateName.bind(this);
        this.doneEditing = this.doneEditing.bind(this);
    }

    toggleEditing() {
        const editing = !this.state.editing;
        this.setState({editing});
    }

    updateName(e) {
        let state = {...this.state};
        state.name = e.target.value;
        this.setState(state);
    }

    doneEditing() {
        let state = {...this.state};
        state.editing = false;
        this.props.edit(this.state.name);
        this.setState(state);
    }

    componentDidMount() {
        
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (prevProps.name !== this.props.name) {
            let state = {...this.state};
            state.name = this.props.name;
            this.setState(state);
        }
    }

    render() {
        const { path, name } = this.props;

        return (
            <>
                {
                    this.state.editing && this.props.edit
                        ?   <>
                                <input value={this.state.name} onChange={this.updateName} />
                                <button onClick={this.doneEditing}>done</button>
                            </>
                        :   <>
                                <Link to={path} style={{textTransform: 'capitalize'}}>{name}</Link>
                                {this.props.edit ? <button onClick={this.toggleEditing}>edit</button> : ''}
                            </>
                }
                
            </>
        );
    }
}

Item.propTypes = {
    path: PropTypes.string.isRequired,
    name: PropTypes.string.isRequired,
    edit: PropTypes.func,
}

export default Item;