import React from 'react';
import { Link, Redirect } from 'react-router-dom';
import BreadCrumbs from '../../Components/BreadCrumbs';
import Ajax from '../../utils/ajax';
import { isDev } from '../../utils/env';

class List extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            redirectToCreate: false,
            projects: [],
        }

        this.redirectToCreate = this.redirectToCreate.bind(this);
        this.deleteProject = this.deleteProject.bind(this);
    }

    redirectToCreate() {
        this.setState({redirectToCreate: true});
    }

    deleteProject(id) {
        if (!isDev()) {
            Ajax.delete(`${this.props.match.url}/${id}`)
                .catch(err => console.error(err));
        }

        this.setState({projects: this.state.projects.filter(p => p.id !== id)});
    }

    componentDidMount() {
        const url = process.env.NODE_ENV === 'development' ? `${process.env.PUBLIC_URL}/data/projects.json` : this.props.match.path;
         
        Ajax.get(url)
            .then(res => {
                console.log(res);
                if (typeof res.projects !== undefined) {
                    this.setState({projects: res.projects});
                }
            })
            .catch(err => console.error(err));
    }

    render() {
        const { match } = this.props;
        return (
             <div className="grid-wrapper">
                <div className="topnav-area"></div>
                <div className="sidebar-area">
                    <ul>
                        <li>item 1</li>
                        <li>item 1</li>
                        <li>item 1</li>
                        <li>item 1</li>

                    </ul>
                
                </div>
                <div className="breadcrumbs-area" >
                    <BreadCrumbs links={[{ name: 'projects', path: '/projects'}]}/>
                </div>
                <div className="content-area">
                    <button onClick={this.redirectToCreate}>New Project</button>
                    { this.state.redirectToCreate ? <Redirect to={`${match.path}/create`} /> : '' }
                    <ul style={{clear: 'both'}}>
                        {
                            this.state.projects.map(p => (
                                <li key={p.id}><Link to={`${match.url}/${p.id}`} >{p.title}</Link><button onClick={() => this.deleteProject(p.id)}>delete</button></li>
                            ))
                        }
                    </ul>
                </div>
            </div>
        );    
    }
    
}

export default List;